:root {
    --primary: navy;
    --link-font-size: 16px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

label {
    display: block;
    margin-top: 5px;
}

input,
select {
    margin-top: 9px;
    width: 200px;
}

button {
    margin-top: 10px;
    padding: 10px 20px;
}


h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Abril Fatface', cursive;
    margin: 0;
    font-size: 32px;

}

p,label,input,textarea,button,select,a,td,th,span{
    font-family: 'Prompt', sans-serif;
    font-size: 16px;
}

p{
    margin-bottom: 15px;
    margin-top: 15px;
}


td,
th {
    padding: 9px;
}
